import { Button, Snackbar } from '@material-ui/core'
import { SnackbarProps } from '@material-ui/core/Snackbar'
import {
  HOUR,
  MINUTE,
  SECOND,
  useInterval,
  useNow,
  useTimer,
} from '@speedlo/hooks'
import { updateWorker, useReleaseCabinet } from '@speedlo/tools'
import { addMilliseconds, formatDistanceStrict } from 'date-fns'
import { useObservable, useObserver } from 'mobx-react-lite'
import React from 'react'

import { formatDateTime } from '../helpers/formats'
import { logUpdate } from '../helpers/logger'
import { useSnackBar } from '../hooks/useSnackBar'
import { i18n } from '../i18n'
import { snackStyles } from './SnackProvider'

export interface IProps {
  onRestart?(): void
  autoUpdateDelay?: number
  postponeTimeout?: number
  updateCheckInterval?: number
  serviceWorker?: ServiceWorkerContainer
  children: ReactNode
}

export const UpdateHandler: React.FC<IProps> = ({
  onRestart = () => window.location.reload(),
  autoUpdateDelay = 20 * SECOND,
  postponeTimeout = 2 * MINUTE,
  updateCheckInterval = process.env.REACT_APP_STAGE === 'prod'
    ? 1 * HOUR
    : 5 * MINUTE,
  serviceWorker = window.navigator.serviceWorker,
  children,
}) => {
  const registrationWorkerRef = React.useRef<ServiceWorkerRegistration | null>(
    null,
  )
  const updateWorkerRef = React.useRef<ServiceWorker | null>(null)

  const state = useObservable({ updatePending: false })

  const { enqueueInfo } = useSnackBar()
  useReleaseCabinet(({ version, date }) => {
    enqueueInfo(`Verze aplikace: ${version}, nasazeno: ${formatDateTime(date)}`)
  })

  useInterval(() => {
    if (registrationWorkerRef.current) {
      logUpdate('checking...')
      registrationWorkerRef.current.update()
    }
  }, updateCheckInterval)

  React.useEffect(
    () => {
      const onMessage = (event: MessageEvent) => {
        if (event.data === 'update-applied') {
          logUpdate('restarting to apply...')
          onRestart()
        }
      }
      if (serviceWorker) {
        serviceWorker.addEventListener('message', onMessage)
        return () => {
          serviceWorker.removeEventListener('message', onMessage)
        }
      } else {
        logUpdate('serviceWorker is not supported')
        return () => {
          /* noop */
        }
      }
    },
    [onRestart, serviceWorker],
  )

  React.useEffect(() => {
    const init = updateWorker.register({
      onInit: reg => {
        registrationWorkerRef.current = reg
      },
      onUpdate: sw => {
        updateWorkerRef.current = sw
        state.updatePending = true
        logUpdate('new update is available')
      },
    })
    // service worker is normally using load event, but since this
    // done after the load, it has to be triggered manually
    init()
  }, [])

  const onCancel = () => {
    logUpdate('user postponed')
    state.updatePending = false
    setTimeout(() => {
      logUpdate('reminding update to user again')
      state.updatePending = true
    }, postponeTimeout)
  }

  const onConfirm = () => {
    state.updatePending = false
    if (updateWorkerRef.current) {
      logUpdate('user confirmed')
      updateWorkerRef.current.postMessage('apply-update')
    }
  }

  return useObserver(() => (
    <>
      {state.updatePending ? (
        <UpdateSnack
          autoHideDuration={autoUpdateDelay}
          onCancel={onCancel}
          onConfirm={onConfirm}
        />
      ) : null}
      {children}
    </>
  ))
}

interface IUpdateSnackProps extends Omit<SnackbarProps, 'open'> {
  autoHideDuration: number
  onConfirm(): void
  onCancel(): void
}

export function UpdateSnack(props: IUpdateSnackProps) {
  const getNow = useNow()
  const { onConfirm, onCancel, ...rest } = props
  const [endTime] = React.useState(() =>
    addMilliseconds(getNow(), props.autoHideDuration),
  )
  const now = useTimer({ interval: SECOND, enabled: true })
  const remainingSeconds = formatDistanceStrict(endTime, now)
  return (
    <Snackbar
      open={true}
      anchorOrigin={{
        vertical: 'top',
        horizontal: 'center',
      }}
      classes={{ root: snackStyles.variantInfo }}
      message={
        <span data-testid="message">{i18n.t`Application will be restarted in ${remainingSeconds} to apply available update.`}</span>
      }
      disableWindowBlurListener={true}
      action={[
        <Button
          key="later"
          color="secondary"
          size="small"
          role="button"
          data-testid="postpone-button"
          onClick={onCancel}
        >
          {i18n.t`Postpone update`}
        </Button>,
        <Button
          key="confirm"
          color="primary"
          size="small"
          role="button"
          data-testid="confirm-button"
          onClick={onConfirm}
        >
          {i18n.t`Restart now`}
        </Button>,
      ]}
      onClose={onCancel}
      {...rest}
    />
  )
}
