import gql from 'graphql-tag'

import { FGeoPointFragmentDoc, FPriceFragmentDoc } from '../../graph/generated'

export const ZoneFragment = gql`
  fragment GZone on Zone {
    id
    name
    enabled
    priority
    companyBranch {
      id
      brand {
        id
      }
    }
    geoPolygon {
      ...FGeoPoint
    }
    priceRangeRules {
      ...GZonePriceRange
    }
    minimalOrderPrice {
      ...FPrice
    }
    zoneColor {
      id
      code
      color
    }
  }

  fragment GZonePriceRange on ZonePriceRangeRule {
    toPrice {
      ...FPrice
    }
    price {
      ...FPrice
    }
  }

  ${FPriceFragmentDoc}
  ${FGeoPointFragmentDoc}
`

export const ZoneLocationQuery = gql`
  query GZoneLocation($zoneId: ID!) {
    zone(id: $zoneId) {
      id
      companyBranch {
        id
        gps {
          ...FGeoPoint
        }
      }
    }
  }
  ${FGeoPointFragmentDoc}
`

export const ZoneBranchLocationQuery = gql`
  query GZoneBranchLocation($branchId: ID!) {
    companyBranch(id: $branchId) {
      id
      gps {
        ...FGeoPoint
      }
    }
  }
  ${FGeoPointFragmentDoc}
`

export const ZoneColorsQuery = gql`
  query GZoneColors {
    zoneColors {
      id
      code
      color
    }
  }
`
