import gql from 'graphql-tag'

import { FGeoPointFragmentDoc } from '../../../graph/generated'

export const OrderBranchLocationQuery = gql`
  query GOrderBranchLocation($branchId: ID!) {
    companyBranch(id: $branchId) {
      id
      gps {
        ...FGeoPoint
      }
    }
  }
  ${FGeoPointFragmentDoc}
`

export const OrderLocationQuery = gql`
  query GOrderLocation($orderId: ID!) {
    order(id: $orderId) {
      id
      gps {
        ...FGeoPoint
      }
    }
  }
  ${FGeoPointFragmentDoc}
`
